import { ReplaySpeedType } from '@tableau/api-external-contract-js';
import { ApiService } from './ServiceRegistry';

/**
 * API Service for interacting with Dashboard Animations
 */
export interface AnimationService extends ApiService {
  /**
   * Replays an animation.
   *
   * @param {ReplaySpeedType} replaySpeedType The replay speed of the animation.
   * @returns {Promise<void>} Promise which resolves to void.
   *
   * @Since 1.7.0
   */
  replayAsync(replaySpeed: ReplaySpeedType): Promise<void>;
}
